$first: #1E1E1D;
$border: #74C991;

footer {
    margin: 30px auto 0 auto;
    position: relative;
    background-color: $first;
    box-shadow: -5px 0 10px rgba(0, 0, 0, 0.597);
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-block: 20px 20px;
    position: relative;
   gap:5px
}

footer {
    p {
        margin-top: 0;
        padding-top: 0;
    }
}

.social {
    text-align: center;

width: 100%;
display: none;
padding-block: 15px;
}

.disclaimer {
   max-width: 920px;
margin: 20px 20px 0 20px;


   p {
    font-size: 11px;
    text-align: center;
    border: 1px solid rgba(255, 255, 255, 0.234);
    padding: 20px;
    
   }
}


.social-links {
    display:flex;
    
    width: 100%;
    justify-content: center;
    
    gap: 10px;
    
}




.tools-div {
    display: flex;
  
   
    gap:8px;
   border-radius: 4px ;

}

.foot-tools {
    width: clamp(1rem, 0.3268rem + 1.1707vw, 1.3rem);
    filter: brightness(130%);
    
    margin-bottom: -5px;

}

.copyright {
    color: #74C991;
    padding-top: 10px;
    font-size: 12px !important;
}

@media screen and (max-width: 980px) {
    footer {
        margin-top: 0;
        box-shadow: none;

      
    }

  .social {
    display: block;
  }
}