
$blue: #9CDCFD;
$border: #74C991;
$first: #1E1E1D;

form {
    margin: 0 30px;
    border-radius: 10px;
   
   
}

input[type=text] {
    background-color: transparent;
    border: none;
  
}
span[contenteditable] {
    padding: 10px;
   z-index: 3;
    outline: none;
    caret-color: rgba(255, 255, 255, 0.542);
    color: white;
width: 100%;
   word-break:break-word;
  
    
}

#message {
    min-height: 200px;
    
}

/* span[contenteditable]::-moz-selection { 
   
    background: #9D9C9D;
  }
  
  span[contenteditable]::selection {

    background: #9D9C9D;
  } */

.input-clicked[contenteditable]::after {
    content: '.';
    height: 16px;
    width: 8px;
    background-color: #9D9C9D;
    color: transparent;
    animation: blinking 1s infinite;

}

@keyframes blinking {
    from { background-color: #9D9C9D }
    to { background-color: transparent; }
}

.input-div {
    display: flex;
    min-height: 40px;
   
    margin-block: 5px 12px;
    border: 1px solid rgba(255, 255, 255, 0.08);

    border-radius: 4px;
   position: relative;

}

label {
margin: 10px;
background-color: $first;
transition: 0.1s;
position: absolute;
}


[contentEditable=true] br {
    display: none;
}

.submit {
    margin-top: 6px;
    cursor: pointer;
    padding: 7px 14px;
    background-color: transparent;
    color: white;
    border: 1px solid $border;
    border-radius: 4px;
    font-size: 1em;
}

.submit:hover {
    transition: 200ms;
    background-color: rgba(255, 255, 255, 0.135);
}

.form-submitted {
    border: 1px solid rgba(255, 255, 255, 0.08);
    height: 300px;
    border-radius: 4px;
    padding: 5px 12px;
}

.confirm-labels {
    color: $blue;
}

.form-submitted > p::after {
    content: '.';
    height: 16px;
    width: 8px;
    background-color: #9D9C9D;
    color: transparent;
    animation: blinking 1s infinite;
}

@keyframes blinking {
    from { background-color: #9D9C9D }
      to { background-color: transparent; }
}

.email-error {transition: 0.2s;
    margin-block: -5px 15px;
    padding-block: 0;
    color: red;
font-size: 12px;
}