
$border: #74C991;

#portfolio {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
   gap: 5px;
   margin-bottom: 20px;
   position: relative;
   overflow: clip;
   padding-block: 40px;
  background-color: black;

  a {
    height: clamp(170px, 30vw,400px);
   
  }
}

.web-info {
    margin-block: 10px;
}

.port-img-bg {
    margin-top: 20px;
    padding-block: 20px;
position:absolute;
   border-radius: 6px 6px 0 0 ;
    padding: 0;
   filter:saturate(20%) blur(10px) ;
  
   opacity:  0.5;
   height: 100vh;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
}

.port-img {
position: relative;
    z-index: 3;
    border-radius: 6px;
    padding: 0;
   
    height: clamp(170px, 30vw,400px);
    outline-offset: 20px;
   
   
  
}

.arrow {
   font-size: 4em !important;
   position: absolute;
   z-index: 4;
   top: 50%;
   transform: translateY(-50%);
   outline: 2px solid $border;
    color: $border;
    background-color: rgba(255, 255, 255, 0.202);
   outline-offset: -3px;
   border-radius: 100px;
   margin: 0;
   
   display: flex;
    justify-content: center;
    align-items: center;
    width: 55px;
    height: 55px;
   
   cursor: pointer;
}

.arrow:hover {
    
    transition:  0.2s;
    
    background-color: rgba(0, 0, 0, 0);
}

.arrow:active {
    transition: 0.1s ;
    filter: none;
}

.arrow-item {
    color: $border;
    font-size: 30px !important;
}


.left {
left: 10px;

}

.right {
    right: 10px;
   
}

.made-with {
  
bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    
  
    position: relative;

}

.port-tools {
    margin-top: 10px;
   display: flex;
  
   

}

.port-tools > img {
    width: clamp(20px, 8vw, 50px);
   
    margin: 0;
}

@media screen and (max-width: 967px) {
    .port-img {
       
    }
}